html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-manrope);
  overflow: hidden;
}

.container {
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: var(--ltGrayishBlue);
}

h1,
h2,
h3 {
  margin-top: 0;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

:root {
  --font-manrope: "Manrope", sans-serif;
}

:root {
  --dkGrayishBlue: hsl(217, 19%, 35%);
  --desatDarkBlue: hsl(214, 17%, 51%);
  --grayishBlue: hsl(212, 23%, 69%);
  --ltGrayishBlue: hsl(210, 46%, 95%);
}

.animate-grow {
  animation-name: animate-grow;
  animation-duration: 1s;
  animation-timing-function: ease-in-out;
  animation-fill-mode: forwards;
  animation-direction: alternate;
}

@keyframes animate-grow {
  0% {
    tranform: scale(1);
  }
  50% {
    transform: scale(1.5);
  }
  100% {
    transform: scale(1);
  }
}
.card {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin-top: 4.375rem;
  padding: 1.25rem;
  align-items: center;
  width: 100%;
  max-width: 23.4375rem;
  border-radius: 0.9375rem;
  color: var(--dkGrayishBlue);
}
@media (min-width: 40em) {
  .card {
    flex-direction: row;
    max-width: 41.875rem;
    height: 17.5rem;
  }
}

.card__top {
  position: relative;
  width: 100%;
  height: 13.75rem;
}
@media (min-width: 40em) {
  .card__top {
    max-width: 17.8125rem;
    height: 17.5rem;
  }
}
.card__top img {
  height: 100%;
  width: 100%;
  max-width: 100%;
  border-top-left-radius: 0.9375rem;
  border-top-right-radius: 0.9375rem;
}
@media (min-width: 40em) {
  .card__top img {
    height: 100%;
    width: 100%;
    object-fit: cover;
    border-bottom-left-radius: 0.9375rem;
    border-top-right-radius: 0rem;
  }
}

.card__wrap {
  background-color: white;
}
@media (min-width: 40em) {
  .card__wrap {
    max-width: 24.0625rem;
    height: 17.5rem;
  }
}

.card__text {
  padding-left: 1.875rem;
  padding-right: 1.875rem;
  padding-top: 1.25rem;
}
.card__text h1 {
  font-weight: 700;
  font-size: 1.09375rem;
}
.card__text p {
  font-size: 0.875rem;
  color: var(--grayishBlue);
  padding-bottom: 0px;
  margin-bottom: 0px;
}
@media (min-width: 40em) {
  .card__text {
    border-top-right-radius: 0.9375rem;
  }
  .card__text h1 {
    font-size: 1.125rem;
  }
  .card__text p {
    font-size: 0.75rem;
    line-height: 1.125rem;
  }
}

.card__avatar {
  display: flex;
  width: 100%;
  padding: 1.25rem;
  background-color: white;
}
@media (min-width: 40em) {
  .card__avatar {
    border-bottom-right-radius: 0.9375rem;
  }
}
@media (min-width: 40em) and (min-width: 40em) {
  .card__avatar {
    padding: 1.8125rem;
  }
}
.card__avatar .avatar {
  flex: 1;
  order: 1;
  width: 80%;
}
.card__avatar .avatar img {
  position: relative;
  padding-top: 0.125rem;
  border-radius: 50%;
  width: 15%;
}
@media (min-width: 40em) {
  .card__avatar .avatar img {
    padding-top: 0.3125rem;
    flex: 0 0 auto;
  }
}
.card__avatar .name {
  display: inline-block;
  padding-left: 0.625rem;
  vertical-align: top;
  font-size: 0.6875rem;
  color: var(--dkGrayishBlue);
  font-weight: 700;
}
.card__avatar .name::after {
  display: block;
  content: "28 Jun 2020";
  font-size: 0.625rem;
  line-height: 1.125rem;
  color: var(--grayishBlue);
}
.card__avatar .arrow1 {
  position: relative;
  order: 2;
  width: 20%;
  text-align: right;
}
@media (min-width: 40em) {
  .card__avatar .arrow1 {
    padding-right: 1.25rem;
  }
}
.card__avatar .btn-arrow {
  position: relative;
  width: 1.875rem;
  height: 1.875rem;
  border: none;
  border-radius: 50%;
  padding: 0.1875rem;
  z-index: 5;
}
.card__avatar .svgElem {
  width: 15px;
  height: 17px;
  transform: translate(9%, 7%);
}
.card__avatar .svgElem.active path {
  fill: var(--ltGrayishBlue);
}

.card__footer,
#shareWindow {
  position: absolute;
  top: 80%;
  width: 89%;
  height: 5rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom-left-radius: 0.9375rem;
  border-bottom-right-radius: 0.9375rem;
  padding: 1.25rem;
  background-color: var(--dkGrayishBlue);
  z-index: 1;
  display: none;
}
@media (min-width: 40em) {
  .card__footer,
#shareWindow {
    top: 40%;
    left: 75%;
    width: 35%;
    height: 18%;
    border-radius: 0.625rem;
  }
}

.card__footer::after {
  content: "";
  display: none;
}
@media (min-width: 40em) {
  .card__footer::after {
    content: "";
    display: block;
    height: 30px;
    width: 30px;
    transform: rotate(45deg);
    position: absolute;
    top: 50%;
    left: 30%;
    background-color: inherit;
    z-index: -1;
  }
}

.text {
  font-size: 0.6875rem;
  color: var(--grayishBlue);
  letter-spacing: 0.3125rem;
}

.icons {
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  list-style: none;
  margin: 0;
  padding: 0;
  height: auto;
  width: auto;
}
.icons img {
  width: 90%;
  padding-right: 0.625rem;
}
.icons li {
  display: flex;
  justify-content: center;
}

@media (min-width: 40em) {
  .arrow2 {
    display: none;
  }
}

.overlayArrow {
  width: 1.875rem;
  height: 1.875rem;
  border: none;
  border-radius: 50%;
  background: var(--ltGrayishBlue);
}

.active {
  background: var(--desatDarkBlue);
}

.svgElem1 {
  width: 15px;
  height: 17px;
  transform: translate(9%, 7%);
}

.svgElem1.active path {
  fill: var(--ltGrayishBlue);
}

.attribution {
  margin-top: -80px;
  font-size: 11px;
  text-align: center;
}

.attribution a {
  color: #3e52a3;
}